import React, { useMemo } from 'react';
import { IBaseIconButtonProps } from './interface';
import { ToolbarHoverTip } from '../../toolbarHoverTip';
import { Wrapper } from './styled';
import { BadgeDot } from '../../../../basic/badge';
import { mergeClassName } from '../../../../common';
import { noop } from 'lodash-es';

export const BaseIconButton = React.memo(React.forwardRef<HTMLDivElement, IBaseIconButtonProps>(
  (props, ref) => {
    const {
      icon,
      title,
      tooltip,
      disabled,
      size,
      active,
      onClick = noop,
      onDoubleClick,
      badgeDot,
      testid,
      role,
      ...restProps
    } = props;

    const [click, doubleClick] = useMemo(() => {
      if (!onDoubleClick) {
        return [onClick, undefined];
      } else {
        return sepreateDoubleClicks(onClick, onDoubleClick);
      }
    }, [onClick, onDoubleClick]);

    const showTitle = size === 'large';

    return (
      <Wrapper
        ref={ref}
        className={mergeClassName('wdn-toolbar-item', size, active && 'active', disabled && 'disabled')}
        data-testid={testid}
        data-role={role}
        onClick={disabled ? undefined : click}
        onDoubleClick={disabled ? undefined : doubleClick}
        {...restProps}
      >
        <ToolbarHoverTip overlayVisible={false} disabled={disabled} tooltip={tooltip || (!showTitle && !disabled && title)}>
          <div className="wdn-toolbar-icon">
            {React.cloneElement(icon, { size: size === 'large' ? 'large' : 'normal' })}
            {
              badgeDot && (
                typeof badgeDot === 'boolean'
                  ? <BadgeDot style={{ position: 'absolute', top: 0, right: 0 }} />
                  : <BadgeDot style={{ position: 'absolute', top: badgeDot.top, right: badgeDot.right }} />
              )
            }
          </div>
        </ToolbarHoverTip>
        {showTitle && <div className="wdn-toolbar-title"><div className="wdn-tb-title-content">{title}</div></div>}
      </Wrapper>
    );
  },
));



const sepreateDoubleClicks = (
  onClick: (e: React.MouseEvent<HTMLElement>) => void,
  onDoubleClick: (e: React.MouseEvent<HTMLElement>) => void
) => {

  let lastSingleClick: number | null = null;

  return [
    (e: React.MouseEvent<HTMLElement>) => {
      if (lastSingleClick === null) {
        const last = lastSingleClick = Date.now();
        setTimeout(() => {
          if (lastSingleClick === last) {
            onClick(e);
            lastSingleClick = null;
          }
        }, 300);
      }
    },
    (e: React.MouseEvent<HTMLElement>) => {
      lastSingleClick = null;
      onDoubleClick(e);
    }
  ];
};

